@import "../../global.scss";

.portfolio{
    background-color: lightskyblue;
    display: flex;
    flex-direction: column;
    align-items: center;

    h1{
        font-size: 50px;
    }

    ul{
        margin: 60px;
        padding: 0;
        list-style: none;
        display: flex;
        
        li{
            font-size: 14px;
            margin-right: 50px;
            padding: 7px;
            border-radius: 10px;
            cursor: pointer;

            &.active{
                background-color: $mainColor;
                color: white;
            }
        }
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
    }

    .item{
        width: 220px;
        height: 400px;
        border-radius: 20px;
        border: 1px solid lightgrey;
        margin: 10px 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        transition: all .5s ease;
        cursor: pointer;
        
        a{
            position: relative;
            font-size: 20px;
            justify-content: space-around;
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 1;
        }

        &:hover{
            background-color: $mainColor;
            img{
                opacity: 0.2;
                z-index: 0;
            }
        }
    }
}